			username: '',
			password:'',
			positionTop:100,
			providerList:[],
			hasProvider:false,
			author:''
		}
	},
	onLoad() {
		this.author=helper.author;
	},
	computed:{...mapState(['baseUrl','hasLogin','forcedLogin','userName'])},//对应store里的数据
	methods: {
		...mapMutations['login'],//不需要强制登录
		WEIXIN_login(){
			console.log('aaaa')
			debugger;
			/* wx.login({
				success:function(res){
					console.log("登陆成功")
					console.log(res)
				},
				fail(res2) {
					console.log("登陆失败")
					console.log(res2)
				}
			}) */
			uni.login({
			  provider: 'weixin',
			  success: function (loginRes) {
				console.log(loginRes);
				console.log(11)
			  },fail() {
			  	console.log('登陆失败')
